.controller-collection {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.controller-collection * {
    pointer-events: auto;
}

.common-controller-container {
    display: block;
    position: absolute;
    right: 0;
}

.angle-selector-container {
    position: absolute;
    bottom: 10px;
}

.speed-controller-container{
    position: absolute;
    bottom: 70px;
    right: 0;
}

.tip-controller-container {
    position: absolute;
    right: 0;
    bottom: 100px;
}

.toggle-btn {
    font-size: 20px;
    padding: 5px;
    margin: 10px;
    border:1px solid gray;
    color: gray;
    cursor: pointer;
}

.toggle-btn:hover {
    background: gray;
    color: white;
    transition: .7s;
}

.toggle-btn.active {
    border-color:#333;
    background: #333;
    color: white;
}

.play-controller-container{
    margin-top: 1rem;
    position: absolute;
    right: 6px;
    bottom: 10px;
}

.play-controller {
    font-size: 30px;
    border: 1px solid gray;
    color: gray;
    padding: 10px;
    cursor: pointer;
}

.play-controller:hover {
    color: white;
    border-color: white;
    transition: .7s;
}

.slider-container {
    margin-top: 1rem;
    display: inline-block;
    width: 300px;
    margin-left: 5px;
    margin-right: 5px;
    color: white;
}

.slider-container.vertical {
    width: 200px;
}

.tip-position-visualizer {
    position: relative;
}

.tip-position {
    position: absolute;
    cursor: pointer;
    pointer-events: none;
    width: 10px;
    height: 10px;
    background: rgba(255, 0, 0, 0.3);
    border-radius: 50%;
    border: 2px solid red;
    top: 50%;
    left: 50%;
}